import axios from "../../node_modules/axios/index";
axios.get("https://conversationrestservice20191209113100.azurewebsites.net/api/conversations").then(function (response) {
    document.getElementById("mainBody").innerHTML += "<ul>";
    response.data.forEach(function (element) {
        document.getElementById("mainBody").innerHTML += "<li>" + element.question + "</li>";
    });
    document.getElementById("mainBody").innerHTML += "</ul>";
});
